import './Links.scss';
import $ from 'jquery';
import { db } from './resources/Firebase';
import { Visitas } from './resources/Helpers';
import { streamer } from './resources/Config';
import { Capitalize } from './resources/Helpers';
import { useEffect, useState } from 'react';
import { datosFirebaseUsuario } from './resources/Firebase';

const Links = () => {
    const [enlaces, setEnlaces] = useState([]);

    useEffect(() => {
        datosFirebaseUsuario()
        .then((data) => {
            $('#nota').html(data.note);
        })

        const arrayDatos = db.collection("users").doc(streamer).collection("buttons").orderBy("timestamp")
        .onSnapshot((arrayEnlaces) => {
            $('#buttons').empty()
            setEnlaces(enlace => enlace.concat(arrayEnlaces.docs))
        }, (error) => {
            console.log(error)
        });

        return () => arrayDatos()        
    }, []);

    return (
        <>
            <div className="row">
                <div className="nota" id="nota"></div>
            </div>
            <div id="buttons">
                {
                    enlaces.length > 0 ? (
                        enlaces.map((enlace,index) => (
                            <>
                            <div className="row">
                                <a className="boton" key={index} rel="noreferrer" onClick={() => Visitas(enlace.data().name)} href={enlace.data().url} target="_blank">
                                    <div className="col">
                                        <img className="icon" key={index} alt={enlace.data().name} src={`./img/${enlace.data().icon}.png`} />
                                        <span className="boton-name">{Capitalize(enlace.data().name)}</span>
                                    </div>
                                </a>
                            </div>
                            </>
                        ))
                    ) : (null)
                }
            </div>
        </>
    )
}

export default Links;